body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

a {
  text-decoration: unset;
}

body {
  color: #c9d1d9;
  background: #161b22;
}

body #root {
  height: 100vh;
}

.FZB5-q_layout {
  height: 100%;
  display: flex;
}

@media screen and (max-width: 800px) {
  .FZB5-q_layout {
    flex-direction: column;
  }
}

.FZB5-q_layout .FZB5-q_left {
  height: 100%;
  width: 400px;
  box-sizing: border-box;
  background: #0e1117;
  border-right: 1px solid #383939;
  flex-shrink: 0;
  align-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

@media screen and (max-width: 800px) {
  .FZB5-q_layout .FZB5-q_left {
    width: 100%;
    height: auto;
  }
}

.FZB5-q_layout .FZB5-q_left .FZB5-q_user {
  text-align: center;
}

.FZB5-q_layout .FZB5-q_left .FZB5-q_user .FZB5-q_photo {
  height: 300px;
}

.FZB5-q_layout .FZB5-q_left .FZB5-q_user .FZB5-q_name {
  color: #c9d1d9;
  margin-bottom: 3px;
}

.FZB5-q_layout .FZB5-q_left .FZB5-q_user .FZB5-q_title {
  color: #c9d1d9;
  margin-top: 0;
  font-size: small;
}

.FZB5-q_layout .FZB5-q_right {
  padding: 50px;
}

.FZB5-q_socialBar .FZB5-q_socialIcon {
  cursor: pointer;
  color: #ccc;
  margin: 10px;
  font-size: 20px;
}

.FZB5-q_socialBar .FZB5-q_socialIcon i {
  color: inherit !important;
}

@keyframes FZB5-q_soc-anim {
  0% {
    color: #ccc;
  }

  50% {
    color: #ddd;
  }

  100% {
    color: #fff;
  }
}

.FZB5-q_socialBar .FZB5-q_socialIcon:hover {
  color: #fff;
  animation-name: FZB5-q_soc-anim;
  animation-duration: 1s;
  animation-timing-function: linear;
}

.FZB5-q_socialIcon {
  margin: 5px;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow {
  height: 50px;
  background: #ffffff05;
  margin: 10px 10px 10px 5px;
  padding: 10px;
  display: flex;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_year {
  margin-right: 10px;
  font-size: small;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_year .FZB5-q_from, .FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_year .FZB5-q_to {
  padding: 5px;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_logo {
  width: 50px;
  flex-shrink: 0;
  margin-right: 10px;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_logo img {
  width: 50px;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_desc {
  margin: 5px;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_desc .FZB5-q_title {
  font-weight: bold;
}

.FZB5-q_educationTable .FZB5-q_educationTableRow .FZB5-q_desc .FZB5-q_sub {
  font-size: small;
}

